*{
  font-family: Helvetica, sans-serif;
  
}
html,body{
  margin:0;
  padding:0;
}

.App {
    text-align: center;
  }
  
  .Container-principal {
    background-color: #ffffff;
  
   justify-content: center;
  
    min-height: 100vh;
    justify-content: center;
    text-align: center;
    align-items: center;
    margin:0;
    padding:0;
    
    padding-top:20px;
  
  }
  .caja-botones{
    display: flex;
    justify-content: space-around;
  }
  
  .caja-botones .boton{
    
    width: 35vw;
    height: 2.5rem;
   
    border-radius: 20px 20px 20px 20px;
    border-style: solid;
    border-color:rgba(179, 179, 179, 0.406);
    border-width: 2px;
    background-color: transparent;
   
  }
  .boton{
    display:flex;
    justify-content: center;
    vertical-align: middle;
    align-items: center;
  }
  .boton img{
    width:15px;
    height: 15px;
    margin-right:5px;
  }
  .boton p{
    color:black;
    font-size: 0.9rem;
   
  }
  
  
  .coche-cercano{
    margin: 5% 5% 5% 6%;
    width: 88%;
    height: 31vh;
    background-color: #F8EEEE;
    border-radius: 30px;
    
    
    
    
  }
  
  .coche-cercano h2,h4{
    margin-left:35px;
  }
  
  .coche-cercano h4{
    margin-top:30px;
    float:left;
    color:rgba(128, 128, 128, 0.543);
    font-size: 0.8rem;
    }
  
  .coche-cercano h2{
  float:left;
  color:black;
  font-size: 1.3rem;
  margin-right:10px;
  padding-right:50px;
  }
  
  .coche-cercano img{
  width: 300px;
  }
  
  .caracteristicas{
    justify-content: center;
    display:inline-flex;
    vertical-align: middle;
    align-items: center;
  }
  
  .caracteristicas .gasolina{
    width:12px;
    height: 12px;
    margin-right:6px;
  }
  .caracteristicas .caminar{
    width:20px;
    height:20px;
    margin-right:2px;
    
  }
  
  .caracteristicas p{
    padding-right:20px;
    margin:auto;
    color:rgba(128, 128, 128, 0.543);
    font-weight: 400;
  }
  
  .container-pequenio{
    justify-content: center;
    margin:0;
  
    width: 100%;
    display:flex;
    grid-template-columns: 45% 45%;
  
  }
  
  
  .container-pequenio .mapa{
    justify-content: center;
    width:40%;
    height: 11rem;
    background-color: black;
    border-radius: 30px;
    margin-right:12px;
  }




  .container-pequenio .propietario{
    justify-content: center;
    
    
    width:40%;
    height: 11rem;
    background-color: #EEEAE6;
    border-radius: 30px;
    margin-left:12px;
  }
  .propietario p{
    color:rgb(58, 58, 58);
    font-weight: 600;
    margin-bottom:2px;
  }
  .propietario .imagen-propietario{
    width: 85%;
    border-radius: 100%;
    align-content: center;
    align-self:center;
  }
  .propietario .estrella{
    width: 20%;
    border-radius: 100%;
    align-self: center;
    justify-content: center;
    justify-self: center;
  }


  .filtros{
    margin: 5% 5% 5% 6%;
    width: 88%;
    height: 31vh;
    background-color: #1C2028;
    border-radius: 30px;
    
  }
  
  .filtros h4{
    margin-top:30px;
    float:left;
    color:rgba(176, 176, 176, 0.543);
    font-size: 0.7rem;
  }
  .filtros p{
    float:right;
    color:rgba(176, 176, 176, 0.543);
    line-height: 24px;
    margin-right:40px;
    font-size: 20px;
    font-weight: bold;
    
  }






